body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
	"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
	sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	height: 100%;

}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
	monospace;
}

.section {
	margin-top: 30px;
}

.section button.section-button {
	margin-left: 10px;
}

.bg-img {
	position: fixed;
	width: 100%;
	height: 100%;
	background-image: url(/static/media/lowpoly.ee41f275.png);
	opacity: 0.7;
	z-index: -1000;
	background-repeat: round;
}
.App {
	/*text-align: center;*/
}

.App-link {
	color: #61dafb;
}

#root, .App {
	height: 100%;
}

.container {
	padding: 10px 20px;
	min-height: 100%;
}

#main {
	background-color: white;
}

#footer {
	padding: 1em 0em;
}

#footer-container {
	background-color: white;
}



